import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Router } from '@angular/router';

import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {

  username = new FormControl("howardchen@twcoding.com");
  password = new FormControl("123");
  body = {};
  httpParams:HttpParams=new HttpParams();
  constructor(
    private http: HttpClient,
    private router: Router
  ) { }

  ngOnInit(): void {
    console.log(this.username.value + '/' + this.password.value);
  }
  login(): void {
    this.httpParams.set('username', this.username.value);
    this.httpParams.set('password', this.password.value);

    this.body = {
      params: this.httpParams
    };

    this.http.post<string[]>('http://localhost:8080/LeaveAsk/login', this.body)
      .subscribe((res) => {
        // this.loginable = res;
        // console.log(res);
        // console.log('check:' + this.loginable[0]);
        // this.loginable1 = this.loginable[0];
        //this.checkuser();
      });
  }
}
